@use "sass:color";

// Variables
$primary-color: #4CAF50;
$text-color: #333;
$background-color: #fff;
$border-radius: 4px;
$transition-duration: 0.3s;

// Colors
$discount-color: #FF4D6D;
$hit-color: #A64DFF;
$hover-color: #A64DFF;

// Breakpoints
$breakpoint-desktop: 1200px;
$breakpoint-tablet: 768px;
$breakpoint-mobile: 480px;

// Mixins
@mixin mobile {
  @media (max-width: $breakpoint-tablet) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: $breakpoint-desktop) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: $breakpoint-mobile) {
    @content;
  }
}

@mixin hover-effect {
  transition: all $transition-duration ease;
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
}

// Reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f2f2f2;
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    align-content: center;
}


// Hero section
.hero {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 645px;
  width: 100%;

  &__video {
    object-fit: cover;
    width: 100%;
    max-width: 1200px;
    height: 517px;
  }

  &__content {
    position: absolute;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    pointer-events: none;
    

    .btn {
      pointer-events: all;
      font-size: 14px;
      font-weight: 500;
      transform: skew(-20deg);
      background-color: #00b67a;
      color: #fff;
      width: 193px;
      height: 33px;
      border: none;
    }
  }
}

// Buttons
.product-card .btn {
  padding: 8px 16px;;
  border: none;
  cursor: pointer;
  

  &--green {
    background-color: #00b67a;
    color: #ffffff;
    width: 144px;
    height: 32px ;
    border-radius: 5px;

    &:hover {
      opacity: 0.9;
    }
  }
}
.product-card.div1 .btn.btn--green{
  border-radius: 5px;
  font-size: 12px;
  font-weight: 700;
}

