// Form styles
.form-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0.3s ease;
    &.active {
        opacity: 1;
        visibility: visible;
    }

    &__content {
        background: #fff;
        padding: 24px 20px 18px 20px;
        border-radius: 5px;
        width: 100%;
        max-width: 387px;
        height: 223px;
        position: relative;
        transform: translateY(20px);
        transition: transform 0.3s ease;
        .active & {
            transform: translateY(0);
        }
    }

    &__close {
        position: absolute;
        top: 15px;
        right: 15px;
        width: 24px;
        height: 24px;
        background: none;
        border: none;
        cursor: pointer;
        padding: 0;
        transition: transform 0.3s ease;

        &:hover {
            transform: rotate(90deg);
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            height: 2px;
            background: $text-color;
            transition: background-color 0.3s ease;
        }

        &::before {
            transform: rotate(45deg);
        }

        &::after {
            transform: rotate(-45deg);
        }

        &:hover::before,
        &:hover::after {
            background: color.adjust($text-color, $lightness: -20%);
        }
    }

    &__title {
        font-size: 24px;
        margin-bottom: 20px;
        color: $text-color;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__input {
        width: 100%;
        max-width: 363px;
        padding: 10 12 10 12;
        border: 1px solid;
        border-radius: 5px;
        font-size: 12px;
        font-weight: 500;
        transition: border-color 0.3s ease;
        margin-bottom: 8px;
        height: 34px;
          &::placeholder {
            font-size: 12px;
            font-weight: 500;
          }

        &:focus {
            outline: none;
            border-color: #00ff04;
        }

        &.error {
            border-color: #E44B4B;
        }
    }

    &__error {
        color: #E44B4B;
        font-size: 8px;
        font-weight: 500;
        margin-top: -20px;
        display: none;
        width: 363px;
        height: 10px;

        &.active {
            display: block;
        }
    }

    &__submit, .btn.btn--green {
        width: 100%;
        max-width: 106px;
        height: 32px;
        border-radius: 5px;
        font-size: 12px;
        font-weight: 700;
        background: #18C576;
        opacity: 33%;
        margin-left: 20px;
        padding: 6px 20px ;
    }
    .form-btn{
      display: flex;
      flex-direction: row ;
      align-items: center;
      max-width: 363px;
      width:100%;
      height: 34px;
    }
    .label-form{
      font-size: 8px;
      font-weight: 500;
      color: #888;
      line-height: 120%;
    }
    h2{
      width: 165px;
      height: 19px;
      padding-bottom: 40px;
      font-size: 20px;
      font-weight: 600;
      text-align: left;
      line-height: 95%;
    }
}
@media (max-width: 768px) {
  .products__grid > .product-card:nth-child(5),
  .products__grid > .product-card:nth-child(6) {
    display: none !important;
  }
}





